* {
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 100px;
  color: #fff;
}

.container {
  display: flex;
  flex-wrap: wrap;
}

.block {
  flex-basis: 300px;
  height: 300px;
  justify-content: center;
  align-items: center;
  flex-grow: 1;
  display: flex;
}

.block--1 {
  background-color: rgb(255, 0, 0);
}

.block--2 {
  background-color: rgb(204, 0, 0);
}

.block--3 {
  background-color: rgb(153, 0, 0);
}

.block--4 {
  background-color: rgb(102, 0, 0);
}

.block--5 {
  background-color: rgb(51, 0, 0);
}

.block--6 {
  background-color: rgb(0, 0, 0);
}

@media (min-width: 600px) {
  .block {
    flex-basis: 50%;
  }

  .block:nth-child(3) {
    order: 2;
  }
  .block:nth-child(4) {
    order: 1;
  }
  .block:nth-child(5),
  .block:nth-child(6) {
    order: 3;
  }
}

@media (min-width: 900px) {
  .block {
    flex-basis: 33%;
  }

  .block:nth-child(1),
  .block:nth-child(2),
  .block:nth-child(3) {
    order: 0;
  }

  .block:nth-child(4) {
    order: 3;
  }
  .block:nth-child(5) {
    order: 2;
  }

  .block:nth-child(6) {
    order: 1;
  }
}

@media (max-width: 599px) {
  .container {
    flex-direction: column;
  }
}
